import React, { useState, useEffect } from 'react'

const Advertisement = () => {
    const [ ad, setAd ] = useState(null)
    useEffect(()=>{
        fetch('ad.json')
        .then(res=>{return res.json()})
        .then(res=>{
        setAd(res.html)
        })
    },[])

    return (
        <div dangerouslySetInnerHTML={{ __html: ad }}/>
    );
}

export default Advertisement;
